<template>
  <div>
    <b-sidebar id="sidebar-right" width="250pt" bg-variant="white" title="" right shadow>
      <div class="px-3 py-2">
        <!-- <b-img src="ass" fluid thumbnail></b-img> -->
        <div class="menu">
          <router-link class="router-link" to="/">Home</router-link>
        </div>
        <div class="menu" @click="changeShowInfo"> </div>
        <div v-if="showInfo==true">
          <div class="side-link">
            <router-link class="router-link" to="/page1"></router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/info/growmethod"> </router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/info/plantdisease"></router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/info/tip"></router-link>
          </div>
        </div>
        <div class="menu" @click="changeShowPlant"> </div>
        <div v-if="showPlant==true">
          <div class="side-link">
            <router-link class="router-link" to="/plant/insideplant">  </router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/plant/dryplant">  </router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/page1">  </router-link>
          </div>
        </div>
        <div class="menu" @click="changeShowLifeStyle"> </div>
        <div v-if="showLifeStyle==true">
          <div class="side-link">
            <router-link class="router-link" to="/life-style/cook"></router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/life-style/book"></router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/life-style/tool"></router-link>
          </div>
        </div>
        <div  class="menu" @click="changeShowCommunity"></div>
        <div v-if="showCommunity==true">
          <div class="side-link">
            <router-link class="router-link" to="/community/colorflower">  </router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/community/mbti">MBTI</router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/community/invention"></router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/community/neologism"></router-link>
          </div>
        </div>
        <div  class="menu" @click="changeShowBoard"> </div>
        <div v-if="showBoard==true">
          <div class="side-link">
            <router-link class="router-link" to="/member/board"></router-link>
          </div>
          <div class="side-link">
            <router-link class="router-link" to="/member/faq">FAQ</router-link>
          </div>
        </div >
        <div class="sign">
          <div>
            <router-link class="router-link" to="/page1"></router-link>
          </div>
          <div>
            <router-link class="router-link" to="/page1"></router-link>
          </div>
        </div>
      </div>
    </b-sidebar>
  </div>
</template>

<script>
export default {
  name: 'Sidebar',
  data() {
    return {
      tool: [],
      showInfo: false,
      showPlant: false,
      showLifeStyle: false,
      showCommunity: false,
      showBoard: false
    }
  },
  methods: {
    changeShowInfo: function() {
      this.showInfo = !this.showInfo
    },
    changeShowPlant: function() {
      this.showPlant = !this.showPlant
    },
    changeShowLifeStyle: function() {
      this.showLifeStyle = !this.showLifeStyle
    },
    changeShowCommunity: function() {
      this.showCommunity = !this.showCommunity
    },
    changeShowBoard: function() {
      this.showBoard = !this.showBoard
    }
  },
  beforeCreate: function() {
    console.log('Sidebar.vue is created')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.py-2 {
  margin: 20pt;
}
.menu {
  font-size: 18pt;
  font-family: 'Noto Sans KR', sans-serif;
  color:black;
  margin: 10pt 0;
}
.side-link {
  font-size: 15pt;
  font-family: 'Noto Sans KR', sans-serif;
  margin: 10pt;
}
.sign {
  font-size: 18pt;
  font-family: 'Noto Sans KR', sans-serif;
  margin: 10pt 0;
  line-height: 45pt;
  bottom: 0;
}
.router-link {
  text-decoration: none;
  color: black;
  margin: 10pt 0;
}
.menu:hover,
.router-link:hover {
  color:#00994C;
  cursor: pointer;
}
</style>


